@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,400;0,500;0,700;1,400&display=swap);
* {
	padding: 0px;
	margin: 0px;
	border: 0px;
 }
 
 *,
 *:before,
 *:after {
	box-sizing: border-box;
 }
 
 :focus,
 :active {
	outline: none;
 }
 
 a:focus,
 a:active {
	outline: none;
 }
 
 aside,
 nav,
 footer,
 header,
 section {
	display: block;
 }
 
 html,
 body {
	height: 100%;
	min-width: 320px;
 }
 
 body {
	line-height: 1;
	font-family: "Ubuntu";
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
 }
 
 input,
 button,
 textarea {
	font-family: "Ubuntu";
 }
 
 input::-ms-clear {
	display: none;
 }
 
 button {
	cursor: pointer;
 }
 
 button::-moz-focus-inner {
	padding: 0;
	border: 0;
 }
 
 a,
 a:visited {
	text-decoration: none;
 }
 
 a:hover {
	text-decoration: none;
 }
 
 ul li {
	list-style: none;
 }
 
 img {
	vertical-align: top;
 }
 
 h1,
 h2,
 h3,
 h4,
 h5,
 h6 {
	font-weight: inherit;
	font-size: inherit;
 }
 
 body {
	background-color: #f5f5f5;
	color: #4d4d4d;
	font-family: 'Ubuntu', sans-serif;
	font-size: 14px;
 }

main {
	width: 100%;
	min-height: 100%;
	display: flex;
	flex-direction: column;
}

